:root {
  --blue-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

ul, li, nav {
  margin: 0;
  padding: 0;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  display: flex;
}

.logo__link {
  width: 40px;
  height: 40px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--blue-color);
  text-decoration: none;
}

li:first-child a {
  margin-left: 0;
}

.is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  height: 4px;
  display: block;
  position: relative;
  top: -4px;
}
